    <el-pagination
      background
      :pager-count="5"
      :current-page=nowpage
      :page-sizes="[6]"
      :page-size=pagesize
      layout="total, sizes, prev, pager, next,jumper"
      :total="total"
      @current-change="changepage">
    </el-pagination>

  </div>
</template>
