          //     for(let i=0;i<pol.length;i++){
          //       if(pol[i]==hjkl){
          //         this.$message({
          //         showClose: true,
          //             message: '抱歉，ID重复，添加失败',
          //             type: 'error'
          //         });
          //         break;
          //       }else{
          //           this.dvp=false
