          this.$api.getlll({})
            .then(res=>{
              const hl=res.data;
              var zx=Number(this.befor.id)
              const jj=hl.includes(zx);
              if(jj && this.befor.id!=this.poq){
                  this.$message({
                  showClose: true,
                      message: '抱歉，ID重复，修改失败',
                      type: 'error'
                  });
              }else{
                this.dvp=false
          this.$api.updatelist({
            id:this.befor.id,
            name:this.befor.name,
            jig:this.befor.jig,
            cid:this.poq
           })
           .then(res=>{
             console.log(this.poq);
          })
