      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
    // 删除，编辑
    handleEdit(index, row) {
        this.dvp = true;
        this.befor=row;
        console.log(row);
        this.ruleForm=row;
        this.poq=row.id;
        console.log(this.poq);
        console.log(this.befor)
      },
      ...mapMutations('shangp',['deletetable']),
      handleDelete(index, row) {
        this.deletetable(row)
        localStorage.setItem('delet',JSON.stringify(row))
        console.log(row);
        // this.$api.deletelist({
        //     id:row.id,
        //     name:row.name,
        //     jig:row.jig
        //     })
        //   .then(res=>{
          
        //     });
        //     window.location.reload();
            // this.nowpage=1;
            // this.$api.getshangplist({
              
            // })
            // .then(res=>{
            //   this.tableData=res.data.data;
            //   this.total=res.data.total;
            //   this.pagesize=res.data.pagesize;
            //  })

      },
    //搜索查询
    search(val){
      if(!val){
        this.http(1);
        this.nowpage=1;
        this.type=1
        return;
      }
      this.$api.getsearch({
        search:val
      })
      .then(res=>{
        console.log('搜索'+res.data);
        this.nowpage=1;
        if(res.data.status===200){
          //处理分页
          this.list=res.data.data;
          this.total=res.data.total;
          this.pagesize=6;
          this.tableData=res.data.data.slice(0,6);
          this.type=2;
        }else{
          this.tableData=[];
          this.total=0;
          this.type=1
        }
      })
    },
    changepage(pages){
      this.$emit('changepage',pages);
      this.nowpage=1
      if(this.type===1){
      this.http(pages)//商品列表分页
    }else{
      //搜索分页
      console.log('搜索分页');
      this.tableData=this.list.slice((pages-1)*6,pages*6)
    }
    },

